<template>
  <div>
    <navbar class="home_nav">
    <div slot="center">SuperMall</div>
    </navbar>
    <input type="text" name="" id="" v-model="username">
    <input type="password" name="" id="" v-model="password">
    <button @click="getHomeGoods(username,password)">注册</button>
  </div>
</template>

<script>

import {getHomeMultidata,getHomeGoods} from 'network/pofile'


import navbar from 'components/common/navbar/navbar'
export default {
  components:{
    navbar
  },
  data(){
    return{
      username:'',
      password:'',
      res:[]
    }
  },
  // created(){
  //   this.getHomeGoods(this.username,this.password)
  // },
  methods:{
    getHomeGoods(username,password){
      getHomeGoods(username,password).then(res=>{
        console.log(res);
        if(res.message=='成功'){
          alert('成功！5秒后自动跳转')
          setTimeout(()=>this.$router.replace('/home'),5000)
        }
        this.res=res
      })
    },
    test(a,b){
      console.log(a,b);
    }
  }
}
</script>

